import React, { Component } from 'react'
import Markdown from 'react-markdown'
import StarRatingComponent from 'react-star-rating-component'
import Link from 'gatsby-link'

class IndexPage extends Component {
  render() {
    const campaigns = this.props.data.campaigns.edges
    console.log(`campaigns: `, campaigns)

    return (
      <div className="main-content outer-wrapper">
        <div className="inner-wrapper cf">
          <div className="content-full">
            <section className="campaigns" style={{ textAlign: `center` }}>
              <h2>
                <Link to="/">All Campaigns</Link>
              </h2>
              <nav>
                <ul style={{ listStyle: `none`, width: `100%` }}>
                  {campaigns.map(({ node }, i) => (
                    <li>
                      {node.campaigns.map(({ id, name }, i) => (
                        <div>
                          <li>
                            <Link
                              to={`/assigments/` + id}
                              style={{ textDecoration: `none` }}
                            >
                              {name}
                            </Link>
                          </li>
                        </div>
                      ))}
                    </li>
                  ))}
                </ul>
              </nav>
            </section>
          </div>
        </div>
      </div>
    )
  }
}

export default IndexPage

export const pageQuery = graphql`
  query getAllCampaignNameSpace {
    campaigns: allCampaignNameSpace {
      edges {
        node {
          id
          namespace
          campaigns {
            id
            name
            contents {
              id
              name
              identity
            }
          }
        }
      }
    }
  }
`



